.section {
  width: 100%;
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  max-width: 1013px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

header.stickyHeader {
  background: white;
  box-shadow: 0px 0px 27px rgba(0, 0, 0, 0.06);
}
header.stickyHeader .header__wrapper .header__container {
  padding-top: 20px;
  padding-bottom: 20px;
  transition: 0.3s;
}

/**
 * TYPOGRAPHY
 */
h1 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 100px;
  line-height: 115px;
  color: #140D00;
  text-transform: uppercase;
}

h2 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 70px;
  line-height: 80px;
  color: #140D00;
  text-transform: uppercase;
}

h3 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  text-transform: uppercase;
  color: #140D00;
  opacity: 0.5;
}

h4 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  color: #140D00;
  opacity: 0.5;
}

p {
  font-family: "Barlow Condensed";
}

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: #140D00;
  text-decoration: none;
  font-family: "Barlow Condensed";
  letter-spacing: 2.7px;
}

.primary___button {
  padding: 27px 75px;
  text-transform: uppercase;
  border: 2px solid #040127;
  background-color: transparent;
  letter-spacing: 2.7px;
}
.primary___button:hover {
  background: linear-gradient(180deg, rgba(20, 13, 0, 0.77) 0%, #140D00 48.96%);
  color: white;
  transition: 0.3s;
}

.secondary___button {
  padding: 27px 75px;
  text-transform: uppercase;
  border: 2px solid transparent;
  letter-spacing: 2.7px;
}
.secondary___button:hover {
  transition: 0.3s;
  border: 2px solid #040127;
}

@media only screen and (max-width: 428px) {
  h1 {
    font-size: 48px;
    line-height: 48px;
  }
  h2 {
    font-size: 40px;
    line-height: 40px;
  }
  h3 {
    font-size: 24px;
  }
  h4 {
    font-size: 24px;
  }
}
header .header__wrapper .header__container {
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header__wrapper .header__container .header__logo {
  width: 45px;
  height: auto;
}
header .header__wrapper .header__container .header__logo img {
  width: 100%;
}
header .header__wrapper .header__container .header__nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 52px;
}
header .header__wrapper .header__container .header__nav a {
  position: relative;
}
header .header__wrapper .header__container .header__nav a:hover:before {
  content: "";
  position: absolute;
  bottom: -21px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #040127;
}
header .header__wrapper .header__container .header__nav a:active:before {
  content: "";
  position: absolute;
  bottom: -21px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #040127;
}

@media only screen and (max-width: 428px) {
  header .header__wrapper .header__container {
    flex-direction: column;
    gap: 20px;
  }
  header .header__wrapper .header__container .header__nav {
    gap: 25px;
  }
  header .header__wrapper .header__container .header__nav a {
    position: relative;
  }
  header .header__wrapper .header__container .header__nav a:hover:before {
    content: "";
    position: absolute;
    bottom: -21px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #040127;
  }
  header .header__wrapper .header__container .header__nav a:active:before {
    content: "";
    position: absolute;
    bottom: -21px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #040127;
  }
}
.hero {
  padding-top: 180px;
}
.hero .main-texts h1 {
  margin-top: 28px;
}
.hero .buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 33px;
  margin-top: 92px;
}

@media only screen and (max-width: 428px) {
  .hero .buttons {
    margin-top: 40px;
  }
}
body {
  position: relative;
}

.bg-logo {
  background-size: cover;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 20%;
  width: 70%;
  z-index: -5;
}

.project-overview {
  background-color: white;
}
.project-overview .project-overview__wrapper .project-overview__container .top__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col1__container .col__image {
  width: 100%;
  overflow: hidden;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col1__container .col__image a img {
  width: 100%;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col1__container .col__image a img:hover {
  transform: scale(1.1);
  transition: 0.5s;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col2__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col2__container .col__image {
  max-width: 505px;
  overflow: hidden;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col2__container .col__image a img {
  width: 100%;
}
.project-overview .project-overview__wrapper .project-overview__container .project-item__container .col2__container .col__image a img:hover {
  transform: scale(1.1);
  transition: 0.5s;
}

@media only screen and (max-width: 428px) {
  .project-overview .project-overview__wrapper .project-overview__container .top__container {
    margin-bottom: 40px;
  }
  .project-overview .project-overview__wrapper .project-overview__container .project-item__container .col2__container {
    flex-direction: column;
    gap: 40px;
  }
}
.technology .technology__wrapper .technology__container .top__container {
  margin-bottom: 56px;
}
.technology .technology__wrapper .technology__container .top__container h2 {
  margin-bottom: 11px;
}
.technology .technology__wrapper .technology__container .top__container h4 {
  margin-top: 11px;
}
.technology .technology__wrapper .technology__container .icons__container {
  max-width: 592px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 47px;
}
.technology .technology__wrapper .technology__container .icons__container .icons__group {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 428px) {
  .technology .technology__wrapper .technology__container .top__container {
    margin-bottom: 40px;
  }
}
.tiles {
  background: white;
}
.tiles .tiles__wrapper .tiles__container .tile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.tiles .tiles__wrapper .tiles__container .tile .tile__image {
  max-width: 450px;
}
.tiles .tiles__wrapper .tiles__container .tile .tile__image img {
  width: 100%;
}
.tiles .tiles__wrapper .tiles__container .tile .tile__texts {
  max-width: 450px;
  display: flex;
  flex-direction: column;
}
.tiles .tiles__wrapper .tiles__container .tile .tile__texts h3 {
  margin-bottom: 5px;
}
.tiles .tiles__wrapper .tiles__container .tile .tile__texts p {
  margin-bottom: 40px;
}
.tiles .tiles__wrapper .tiles__container .tile .tile__texts .primary___button {
  width: max-content;
}
.tiles .tiles__wrapper .tiles__container .tile.tile-reversed {
  flex-direction: row-reverse;
}
.tiles .tiles__wrapper .tiles__container .tile + .tile {
  margin-top: 100px;
}

@media only screen and (max-width: 428px) {
  .tiles .tiles__wrapper .tiles__container .tile {
    flex-direction: column;
  }
  .tiles .tiles__wrapper .tiles__container .tile.tile-reversed {
    flex-direction: column;
  }
}
.contact .hero .hero__wrapper .hero__container .main-texts p {
  margin-top: 28px;
}
.contact .hero .hero__wrapper .hero__container .main-texts p a.email {
  font-weight: 700;
  text-transform: uppercase;
}